<template>
  <div class="main">
    <div class="tabs">
      <div :class="{active:tab == 2}" @click="tab = 2">单井钻头统计图</div>
      <div :class="{active:tab == 1}" @click="tab = 1">钻头统计展示</div>
    </div>
    <el-table :data="list" border style="width: 100%" v-show="tab == 1">
      <template v-for="(item,i) in arr">
        <el-table-column v-if="item.child" :label="item.txt" :key="i">
          <el-table-column v-for="(l,j) in item.child" :prop="l.prop" :label="l.txt" :key="j"></el-table-column>
        </el-table-column>
        <el-table-column v-else :prop="item.prop" :label="item.txt" :key="i"></el-table-column>
      </template>
    </el-table>
    <div v-show="tab == 2" id="char" ></div>
  </div>
</template>

<script>
import echarts from 'echarts'
import request from '@/utils/request'
import {borebit} from '@/config/singlewell'
export default {
  props: ["route"],
  data () {
    return {
      tab: 2,
      arr: borebit,
      list: [],
    }
  },

  components: {},

  computed: {},

  mounted: function() {
    let params = this.route;
    request({
      url: '/singlewell/bit',
      method: 'get',
      params
    }).then(res => {
      this.list = res;
    })
    this.draw()
  },

  methods: {
    draw() {
      let char = echarts.init(document.getElementById('char'));
      let option = {
        tooltip : {
          trigger: 'axis',
          axisPointer : {            // 坐标轴指示器，坐标轴触发有效
            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          },
          formatter: function (params) {//console.log(params)
            return params[1].name + ' : ' + params[1].value + '<br/>' + params[2].seriesName + ' : ' + params[2].value
          }
        },
        legend: {
            data:['钻头','机械转速']
        },
        yAxis: [{
          type : 'category',
          data :  function (){
            var list = [];
            for (var i = 1; i <= 6; i++) {
                list.push('钻头' + i);
            }
            return list;
          }()
        },
        {
          type : 'category',
          // name: '机械转速',
          // nameLocation: 'center',
          // splitLine: {show:false},
          data :  function (){
            var list = [];
            for (var i = 1; i <= 6; i++) {
                list.push('机械钻速' + i);
            }
            return list;
          }()
        }],
        xAxis: [
          {
            type : 'value',
            name: '井深',
            max: 5000,
            nameLocation: 'center',
            axisLabel: {
              formatter: '{value} m'
            }
          },
          {
            type: 'value',
            name: '转速',
            inverse: true,
            max: 100,
            nameLocation: 'center',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '辅助',
            type: 'bar',
            stack: '总量',
            itemStyle: {
              normal: {
                barBorderColor: 'rgba(0,0,0,0)',
                color: 'rgba(0,0,0,0)'
              },
              emphasis: {
                barBorderColor: 'rgba(0,0,0,0)',
                color: 'rgba(0,0,0,0)'
              }
            },
            data: [0, 900, 1245, 1645, 1845, 2045]
          },
          {
            name: '钻头',
            type: 'bar',
            stack: '总量',
            barGap: '-100%',
            label: {
              normal: {
                show: true,
                position: 'right'
              }
            },
            data: [900, 345, 400, 200, 200, 135]
          },
          {
            name: '机械转速',
            type: 'bar',
            xAxisIndex: 1,
            barGap: '-100%',
            label: {
              normal: {
                show: true,
                position: 'left'
              }
            },
            data: [30,20,10,20,30,40]
          }
        ]
      };
      char.setOption(option,true)
    }
  }
}
</script>

<style lang='scss' scoped>
.main {
  background: #27496adc;
  min-width: 1080px;
  width: 90%;
  height: calc(100% - 40px);
  padding: 30px;
  margin: 20px auto;
  box-sizing: border-box;
  border: 1px solid #30b8e2;
  border-radius: 3px;
  overflow: auto;
}
.tabs {
  display: flex;
  margin-bottom: 20px;
  div {
    margin-right: 16px;
    color: #fff;
    padding-bottom: 4px;
    cursor: pointer;
    &.active {
      border-bottom: 2px solid #fff;
    }
  }
}
#char {
  height: calc(100% - 50px);
  background: #fff;
}
</style>



