let borebit = [
    {
        txt: '序号',
        prop: 'bit_no'
    },
    {
        txt: '尺寸',
        prop: 'bit_size'
    },
    {
        txt: '钻头型号',
        prop: 'bit_model'
    },
    {
        txt: '厂家',
        prop: 'producer'
    },
    {
        txt: '钻遇地层',
        prop: 'layer'
    },
    {
        txt: '岩性',
        prop: 'lithology'
    },
    {
        txt: '入井深度',
        prop: 'start_depth'
    },
    {
        txt: '出井深度',
        prop: 'end_depth'
    },
    {
        txt: '进尺',
        prop: 'footage'
    },
    {
        txt: '机械钻速',
        prop: 'rop'
    },
    {
        txt: '钻压',
        prop: 'wob'
    },
    {
        txt: '转速',
        prop: 'rpm'
    },
    {
        txt: '排量',
        prop: 'flow'
    },
    {
        txt: '钻井液密度',
        prop: 'density'
    },
    {
        txt: '钻井方式',
        prop: 'drill_type'
    }
]





export {
    borebit
}


